.sb-badge {
  background-color: var(--sb-badge-color, var(--sbt-theme-color-70));
  color: var(--sbt-color-text-invert);
  display: inline-block;
  font-weight: var(--sbt-font-weight-60);
  height: var(--sb-badge-size);
  line-height: var(--sb-badge-size);
  position: relative;
  text-align: center;
}

.sb-badge_light {
  --sb-button-color: var(
    --sb-badge-color-darker,
    var(--sbt-theme-tertiary-color-20)
  );
  background-color: var(--sb-badge-color-lighter, var(--sbt-theme-color-30));
  color: var(--sb-badge-color, var(--sbt-theme-color-90));
}

.sb-badge_primary {
  --sb-badge-color: var(--sbt-theme-color-70);
  --sb-badge-color-light: var(--sbt-theme-color-40);
  --sb-badge-color-lighter: var(--sbt-theme-color-30);
  --sb-badge-color-darker: var(--sbt-theme-color-90);
  --sb-badge-color-darkest: var(--sbt-theme-color-100);
}

.sb-badge_secondary {
  --sb-badge-color: var(--sbt-slate-60);
  --sb-badge-color-light: var(--sbt-slate-30);
  --sb-badge-color-lighter: var(--sbt-slate-20);
  --sb-badge-color-darker: var(--sbt-slate-90);
  --sb-badge-color-darkest: var(--sbt-slate-100);
}

.sb-badge_base {
  --sb-badge-color: var(--sbt-theme-tertiary-color-50);
  --sb-badge-color-light: var(--sbt-theme-tertiary-color-30);
  --sb-badge-color-lighter: var(--sbt-theme-tertiary-color-20);
  --sb-badge-color-darker: var(--sbt-theme-tertiary-color-80);
  --sb-badge-color-darkest: var(--sbt-theme-tertiary-color-90);
}

.sb-badge_error {
  --sb-badge-color: var(--sbt-color-error-50);
  --sb-badge-color-light: var(--sbt-color-error-30);
  --sb-badge-color-lighter: var(--sbt-color-error-10);
  --sb-badge-color-darker: var(--sbt-red-90);
  --sb-badge-color-darkest: var(--sbt-red-100);
}

.sb-badge_success {
  --sb-badge-color: var(--sbt-color-success-50);
  --sb-badge-color-light: var(--sbt-green-30);
  --sb-badge-color-lighter: var(--sbt-color-success-10);
  --sb-badge-color-darker: var(--sbt-green-90);
  --sb-badge-color-darkest: var(--sbt-green-100);
}

.sb-badge_warning {
  --sb-badge-color: var(--sbt-color-warning-50);
  --sb-badge-color-light: var(--sbt-orange-30);
  --sb-badge-color-lighter: var(--sbt-color-warning-10);
  --sb-badge-color-darker: var(--sbt-yellow-100);
  --sb-badge-color-darkest: var(--sbt-orange-100);
}

.sb-badge_active {
  --sb-badge-color: var(--sbt-indigo-60);
  --sb-badge-color-light: var(--sbt-indigo-30);
  --sb-badge-color-lighter: var(--sbt-indigo-20);
  --sb-badge-color-darker: var(--sbt-indigo-90);
  --sb-badge-color-darkest: var(--sbt-indigo-100);
}

.sb-badge_circle,
.sb-badge_dot {
  border-radius: var(--sb-badge-size);
  width: var(--sb-badge-size);
}

.sb-badge_icon {
  --sb-icon_padding: 0;
  align-items: center;
  display: inline-flex;
  justify-content: center;
}

.sb-badge_pill {
  border-radius: var(--sb-badge-size);
  padding: 0 calc(var(--sb-badge-size) / 2);
  width: auto;
}

.sb-badge_closable {
  padding-right: var(--sb-badge-size);

  i {
    line-height: var(--sbt-line-height-0);
  }
}

.sb-badge_smaller {
  --sb-badge-size: 16px;
  font-size: var(--sbt-font-size-15);

  &.sb-badge_dot {
    --sb-badge-size: 4px;
  }
}

.sb-badge_small {
  --sb-badge-size: 24px;
  font-size: var(--sbt-font-size-20);

  &.sb-badge_dot {
    --sb-badge-size: 8px;
  }
}

.sb-badge_medium {
  --sb-badge-size: 32px;
  font-size: var(--sbt-font-size-25);

  &.sb-badge_dot {
    --sb-badge-size: 12px;
  }
}

.sb-badge_large {
  --sb-badge-size: 40px;
  font-size: var(--sbt-font-size-30);

  &.sb-badge_dot {
    --sb-badge-size: 16px;
  }
}

.sb-badge_width-large {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  width: 16ch;
}

.sb-badge_status {
  align-items: center;
  background-color: transparent;
  border-radius: var(--sbt-border-radius-large);
  color: var(--sb-typography-color);
  display: inline-flex;
  padding: 0 var(--sbt-spacing-40);

  &.sb-badge_smaller {
    --sb-icon__font-size: 6px;
    --sb-icon__size: 8px;
  }

  &.sb-badge_small {
    --sb-icon__font-size: 6px;
    --sb-icon__size: 10px;
  }

  &.sb-badge_medium {
    --sb-icon__font-size: 6px;
    --sb-icon__size: 10px;
  }

  &.sb-badge_large {
    --sb-icon__font-size: 6px;
    --sb-icon__size: 10px;
  }

  &.sb-badge_success {
    --sb-icon__color: var(--sbt-color-success-50);
    border: var(--sbt-stroke-standard) solid var(--sbt-color-success-50);
  }

  &.sb-badge_warning {
    --sb-icon__color: var(--sbt-color-warning-50);
    border: var(--sbt-stroke-standard) solid var(--sbt-color-warning-50);
  }

  &.sb-badge_error {
    --sb-icon__color: var(--sbt-color-error-50);
    border: var(--sbt-stroke-standard) solid var(--sbt-color-error-50);
  }

  &.sb-badge_primary {
    --sb-icon__color: var(--sbt-theme-color-70);
    border: var(--sbt-stroke-standard) solid var(--sbt-theme-color-70);
  }

  &.sb-badge_secondary {
    --sb-icon__color: var(--sbt-theme-secondary-color-70);
    border: var(--sbt-stroke-standard) solid var(--sbt-theme-secondary-color-70);
  }

  &.sb-badge_base {
    --sb-icon__color: var(--sbt-theme-tertiary-color-70);
    border: var(--sbt-stroke-standard) solid var(--sbt-theme-tertiary-color-70);
  }

  &.sb-badge_active {
    --sb-icon__color: var(--sbt-indigo-60);
    border: var(--sbt-stroke-standard) solid var(--sbt-indigo-60);
  }
}

.sb-badge_status-icon {
  margin-right: var(--sbt-spacing-10);
}
