// --sb-step_badge_completed-color - controls the text and background color of the badge when completed
// --sb-step_badge-icon_completed-color - controls the icon color of the badge when completed
// --sb-step_badge_in-progress-color - controls the text and background color of the badge when in progress
// --sb-step_badge-icon_in-progress-color - controls the icon color of the badge when in progress
// --sb-step_badge_unstarted-color - controls background color of the badge when unstarted
// --sb-step_badge-icon_unstarted-color - controls the icon and border color of the badge when unstarted
// --sb-step_connector_completed-color - controls the color of the connector when completed
// --sb-step_connector_unstarted-color - controls the color of the connector when completed
// --sb-step_connector_width - controls the width of the connector
// --sb-step_text_completed-color - controls the text color when completed
// --sb-step_text_in-progress-color - controls the text color when in progress
// --sb-step_text_unstarted-color - controls the text color when unstarted

.sb-step {
  display: flex;
  flex: 1 0 auto;
}

.sb-step_connected {
  &:not(:last-child)::after {
    align-self: center;
    border-bottom: 2px solid
      var(--sb-step_connector_unstarted-color, var(--sbt-theme-color-70));
    content: '';
    display: flex;
    flex: 1 0 auto;
    width: var(--sb-step_connector_width, 2em);
  }

  &.sb-step_completed:not(:last-child)::after {
    border-bottom: 2px solid
      var(--sb-step_connector_completed-color, var(--sbt-color-success-50));
  }

  &:last-child {
    flex: 0 0 auto;
  }
}

.sb-step_unconnected {
  margin: 0 1em;

  &:first-child {
    margin-left: 0;
  }

  &:last-child {
    flex: 0 0 auto;
    margin-right: 0;
  }
}

.sb-step_both {
  align-items: center;
  display: flex;
  justify-content: space-evenly;

  > :last-child {
    margin-left: 0.5em;
  }
}

.sb-step-badge {
  min-width: 16px;
}

.sb-step-badge_unstarted {
  --sb-badge-color: var(--sb-step_badge_unstarted-color, var(--sbt-white));
  border: 1px solid
    var(--sb-step_badge-icon_unstarted-color, var(--sbt-theme-color-70));

  color: var(--sb-step_badge-icon_unstarted-color, var(--sbt-theme-color-70));
}

.sb-step-badge_in-progress {
  --sb-badge-color: var(
    --sb-step_badge_in-progress-color,
    var(--sbt-theme-color-70)
  );

  color: var(--sb-step_badge-icon_in-progress-color, var(--sbt-white));
}

.sb-step-badge_completed {
  --sb-badge-color: var(
    --sb-step_badge_completed-color,
    var(--sbt-color-success-50)
  );

  color: var(--sb-step_badge-icon_completed-color, var(--sbt-white));
}

.sb-step_connected_text_unstarted {
  color: var(--sb-step_text_unstarted-color, var(--sbt-theme-color-70));
}

.sb-step_connected_text_in-progress {
  color: var(--sb-step_text_in-progress-color, var(--sbt-white));
}

.sb-step_connected_text_completed {
  color: var(--sb-step_text_completed-color, var(--sbt-white));
}

.sb-step_unconnected_text_unstarted {
  color: var(
    --sb-step_text_unstarted-color,
    var(--sbt-theme-tertiary-color-40)
  );
}

.sb-step_unconnected_text_in-progress {
  color: var(--sb-step_text_in-progress-color, var(--sbt-theme-color-70));
}

.sb-step_unconnected_text_completed {
  color: var(--sb-step_text_completed-color, var(--sbt-color-success-50));
}

.sb-step_vertical {
  &.sb-step_unconnected {
    margin: 1em 0;

    &:first-child {
      margin-top: 0;
    }

    &:last-child {
      flex: 0 0 auto;
      margin-bottom: 0;
    }
  }

  &.sb-step_connected {
    flex-direction: column;

    &:not(:last-child)::after {
      border: 1px solid
        var(--sb-step_connector_unstarted-color, var(--sbt-theme-color-70));
      height: var(--sb-step_connector_width, 2em);
      width: auto;
    }

    &.sb-step_completed:not(:last-child)::after {
      border-color: var(
        --sb-step_connector_completed-color,
        var(--sbt-color-success-50)
      );
    }
  }
}
